.achievements {
  background: #F2F2F2;
}

@media (min-width: @tablet-width) {
  .achievements__wrapper {
    width: 616px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border: 2px solid #E5E5E5;
    border-radius: 1px;
    background: #FFFFFF;
  }

  .achievements {
    background: #FFFFFF;
  }

  .achievements__title {
    text-align: center;
    text-transform: uppercase;
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;
    color: #D22856;
  }

  .achievements__list {
    display: flex;
    flex-direction: column;
  }

  .achievements {
    background: #FFFFFF;
  }
}

@media (min-width: @desktop-width) {
  .achievements__wrapper {
    width: 936px;
    display: flex;
    justify-content: space-between;
    border: 2px solid #E5E5E5;
    border-radius: 1px;
  }

  .achievements__title {
    text-align: center;
    text-transform: uppercase;
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;
    color: #D22856;
  }

  .achievements__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: unset;
    margin: 0;
    padding-inline-start: 60px;
    padding-inline-end: 60px;
  }

  .achievements__item {
    display: flex;
    align-items: center;
  }

  .achievements__text {
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
  }

  .achievements__input {
    margin-right: 22px;
  }
}

.achievements__title {
  text-align: center;
  text-transform: uppercase;
  color: #D22856;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
}

.achievements__list {
  list-style: none;
}

.achievements__text {
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  color: #283645;
}
